import { Component, OnInit, ChangeDetectorRef } from '@angular/core';
import { AuthService } from '../../../providers/auth.service';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
declare var window: any;

@Component({
  selector: 'app-video-meeting',
  templateUrl: './video-meeting.component.html',
  styleUrls: ['./video-meeting.component.scss']
})
export class VideoMeetingComponent implements OnInit {

  fetching: boolean = true;
  api: any;
  constructor(
    public service: AuthService,
    public activeModal: NgbActiveModal,
    public cd: ChangeDetectorRef
  ) { }

  ngOnInit() {
    localStorage.setItem('videoMeetingOpen', JSON.stringify('true'));
    this.initializeMeeting();
    setTimeout(() => {
      this.fetching = false;
      window.jQuery('.modal-dialog').addClass('modal-dialog modal-lg');
    }, 1000);
  }

  initializeMeeting() {
    let data: any = this.service.getCurrentUser();
    if (data) {
      const domain = 'meet.jit.si';
      const options = {
        roomName: 'JitsiMeetAPIExample',
        height: 600,
        userInfo: {
          email: data.email,
          displayName: data.displayName,
        },
        parentNode: document.querySelector('#meet')
      };
      this.api = new window.JitsiMeetExternalAPI(domain, options);
    }
  }

  dispose() {
    if (this.api) {
      this.api.dispose();
    }
    this.activeModal.close();
  }
}
