import { Component } from '@angular/core';
import { CreateParticipantComponent } from '../create-participant/create-participant.component';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { Router } from '@angular/router';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent {

  constructor(
    public modalService: NgbModal,
    public router: Router
  ) { }

  createParticipant() {
    if (!localStorage.getItem('participant')) {
      this.modalService.open(CreateParticipantComponent, {
        backdrop: 'static',
        keyboard: false,
      });
    }
  }

  navigate(routerName) {
    this.router.navigateByUrl('/' + routerName);
    var ele = document.getElementById('main_div');
    if (ele) {
      ele.scrollIntoView({ behavior: "smooth" });
    }
  }
}
