import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { HeaderComponent } from './header/header.component';
import { FooterComponent } from './footer/footer.component';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from './components/home/home.component';
import { BaseComponent } from './base.component';
import { AboutComponent } from './components/about/about.component';
import { SpakersComponent } from './components/spakers/spakers.component';
import { ScheduleComponent } from './components/schedule/schedule.component';
import { ContactComponent } from './components/contact/contact.component';
import { BlogComponent } from './components/blog/blog.component';
import { BlogDetailsComponent } from './components/blog-details/blog-details.component';
import { ElementsComponent } from './components/elements/elements.component';
import { CreateParticipantComponent } from './components/create-participant/create-participant.component';
import { NgbModule, NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { ExpoComponent } from './components/expo/expo.component';
import { ProductInfoComponent } from './components/product-info/product-info.component';
import { VideoMeetingComponent } from './components/video-meeting/video-meeting.component';

const routes: Routes = [
  {
    path: '',
    component: BaseComponent,
    children: [
      { path: '', redirectTo: 'home', pathMatch: 'full' },
      { path: 'home', component: HomeComponent },
      { path: 'about', component: AboutComponent },
      { path: 'spakers', component: SpakersComponent },
      { path: 'schedule', component: ScheduleComponent },
      { path: 'expo', component: ExpoComponent },
      { path: 'blog', component: BlogComponent },
      { path: 'blog-details', component: BlogDetailsComponent },
      { path: 'elements', component: ElementsComponent },
      { path: 'contact', component: ContactComponent },
    ]
  },
];

@NgModule({
  declarations: [
    HeaderComponent,
    FooterComponent,
    HomeComponent,
    BaseComponent,
    AboutComponent,
    SpakersComponent,
    ScheduleComponent,
    ContactComponent,
    BlogComponent,
    BlogDetailsComponent,
    ElementsComponent,
    CreateParticipantComponent,
    ExpoComponent,
    ProductInfoComponent,
    VideoMeetingComponent
  ],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgbModule,
  ],
  exports: [
    RouterModule,
    HeaderComponent,
    FooterComponent,
    CreateParticipantComponent
  ],
  entryComponents: [
    CreateParticipantComponent,
    ProductInfoComponent,
    VideoMeetingComponent
  ],
  providers: [NgbActiveModal]
})
export class BaseModule { }
