import { Component, OnInit } from '@angular/core';
import { AuthService } from 'src/app/providers/auth.service';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
declare var window: any;

@Component({
  selector: 'app-product-info',
  templateUrl: './product-info.component.html',
  styleUrls: ['./product-info.component.scss']
})
export class ProductInfoComponent implements OnInit {

  vendorDetails: any = {};
  products: any = [];
  productYoutubeLink: string = '';
  productName: string = '';
  fetching: boolean = true;

  constructor(
    public service: AuthService,
    public activeModal: NgbActiveModal
  ) { }

  ngOnInit() {
    localStorage.setItem('productModalOpen', JSON.stringify('true'));
    setTimeout(() => {
      window.jQuery('.modal-dialog').addClass('modal-dialog modal-lg');
    }, 1000);
    this.getProductDetails();
  }

  getProductDetails() {
    this.service.getVendorDetails('RSDa2MJhiiR8U2MgiAHN9yPhKcy1').subscribe(res => {
      this.vendorDetails = res;
      if (this.vendorDetails && this.vendorDetails.productDetails.length) {
        this.products = this.vendorDetails.productDetails;
        this.showSelectedProduct(this.vendorDetails.productDetails[0]);
      }
      this.fetching = false;
    })
  }

  showSelectedProduct(product) {
    this.productName = product.productName;
    this.productYoutubeLink = product.provideYouTubeVideolink;
  }

  close() {
    this.activeModal.close();
  }

  getproductImage(product) {
    if (product.productDetailsProductImage && product.productDetailsProductImage.length && product.productDetailsProductImage[0].url) {
      return product.productDetailsProductImage[0].url;
    }
    return 'https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcTYWuhcxmhBdb1B2vXhXAGAFn-XdyyUmQFfQw&usqp=CAU'
  }
}
