import { Component, OnInit, ChangeDetectionStrategy, ChangeDetectorRef } from '@angular/core';
import { NgbModal, NgbModalOptions } from '@ng-bootstrap/ng-bootstrap';
import { LoginComponent } from 'src/app/auth/login/login.component';
import { Router } from '@angular/router';
import { AngularFirestore } from '@angular/fire/firestore';
import { ProductInfoComponent } from '../product-info/product-info.component';
import { VideoMeetingComponent } from '../video-meeting/video-meeting.component';

@Component({
  selector: 'app-expo',
  templateUrl: './expo.component.html',
  styleUrls: ['./expo.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class ExpoComponent implements OnInit {

  showIframe: boolean = false;
  ngbModalOptions: NgbModalOptions = {
    backdrop: 'static',
    keyboard: false
  };
  constructor(
    public modal: NgbModal,
    public router: Router,
    public firestore: AngularFirestore,
    public cd: ChangeDetectorRef
  ) { }

  ngOnInit() {
    localStorage.removeItem('productModalOpen');
    localStorage.removeItem('videoMeetingOpen');
    this.openLoginModal();
    let that = this;

    window.addEventListener("message", function (event) {
      if(event && event.origin !== 'https://www.watchitvirtually.com'){
        return false;
      }

      if ((event.data != 'tourLoaded') && localStorage.getItem('participant')) {
        var userId: any = JSON.parse(localStorage.getItem('participant'));
        var date: any = new Date();
        
        setTimeout(() => {
          that.firestore.collection('Analytics').add({ date: date, event: event.data, participantId: userId.user.uid });
          // analytics = firebase.analytics();
          // analytics.logEvent('login', { date: date, event: event.data, participantId: userId.user.uid });
        }, 1000)

        if (event.data == 'Designmation_ProductInfoVideo') {
          that.openProductModal();
        } else if (event.data == 'EXIT_Designmation') {
          that.openVideoMeeting();
        }
      }
    });
  }

  openLoginModal() {
    if (!localStorage.getItem('participant')) {
      this.modal.open(LoginComponent, this.ngbModalOptions).result.then(res => {
        if (res) {
          this.showIframe = true;
          this.cd.detectChanges();
        }
      })
    } else {
      this.showIframe = true;
    }
    this.cd.detectChanges();
  }

  openVideoMeeting() {
    this.modal.open(VideoMeetingComponent, this.ngbModalOptions).result.then(() => {
      localStorage.removeItem('videoMeetingOpen');
    });
  }

  openProductModal() {
    this.modal.open(ProductInfoComponent, this.ngbModalOptions).result.then(() => {
      localStorage.removeItem('productModalOpen');
    });
  }
}
