import { Component, OnInit, ChangeDetectionStrategy } from '@angular/core';
import { CreateParticipantComponent } from '../components/create-participant/create-participant.component';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { LoginComponent } from 'src/app/auth/login/login.component';
import { Router } from '@angular/router';
import { AuthService } from 'src/app/providers/auth.service';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class HeaderComponent implements OnInit {
  closeResult: string;

  constructor(
    private modalService: NgbModal,
    public router: Router,
    public service: AuthService
  ) { }

  ngOnInit() {

    var dynamicScripts = [
      './assets/js/main.js'
    ];

    for (var i = 0; i < dynamicScripts.length; i++) {
      let node = document.createElement('script');
      node.src = dynamicScripts[i];
      node.type = 'text/javascript';
      node.async = false;
      node.charset = 'utf-8';
      document.getElementsByTagName('head')[0].appendChild(node);
    }
  }

  createParticipant() {
    if (!localStorage.getItem('participant')) {
      this.modalService.open(CreateParticipantComponent, {
        backdrop: 'static',
        keyboard: false
      });
    }
  }

  openLogin() {
    if (!localStorage.getItem('participant')) {
      this.modalService.open(LoginComponent).result.then(res => {
        if (res) {
          this.router.navigateByUrl('/expo');
        }
      })
    } else {
      this.router.navigateByUrl('/expo');
    }
  }

  enableView: boolean = false;
  toggle() {
    this.enableView = !this.enableView
  }

  navigate(routerName, mobileView?: any) {
    this.router.navigateByUrl('/' + routerName);
    var ele = document.getElementById('main_div');
    if (ele) {
      ele.scrollIntoView({ behavior: "smooth" });
    }
    if (mobileView) {
      this.enableView = false;
    }
  }
}
